header {
	background:$primary-blue;
	height:40px;

	/* LOGO  & SEARCH FIELD*/
	.header--search_div {
		max-height: 100%;
		max-width:100px;
		padding: 10px 80px 10px 70px;
	}

	#headerLogo {
		max-height:100%;
		max-width:100%;
		#headerLogo--img {
			/* these two are so the image fits to the div */
			max-height: 100%;
			max-width: 100%;
		}
	}

	#searchBox {
		width: 80px;
		height: 100%;
		border-radius:10px;
	}
}
